'use client';

import { ColumnDef } from '@tanstack/react-table';
import { Beneficiary } from '@/lib/generated/prisma';
import { Button } from '@/components/ui/button';

type BeneficiaryWithRelations = Beneficiary & {
  createdBy?: {
    firstName: string;
    lastName: string;
  };
  institution?: {
    id: string;
    name: string;
  };
  beneficiaryMessages?: { id: string; title?: string }[];
  beneficiaryRequests?: { id: string; title?: string }[];
};

export function beneficiaryColumns({
  onEdit,
  onDelete,
  onAddMessage,
  onAddRequest,
  onMessageIdClick,
  onRequestIdClick,
}: {
  onEdit: (beneficiary: BeneficiaryWithRelations) => void;
  onDelete: (beneficiaryId: string) => void;
  onAddMessage?: (beneficiaryId: string) => void;
  onAddRequest?: (beneficiaryId: string) => void;
  onMessageIdClick?: (msgId: string) => void;
  onRequestIdClick?: (reqId: string) => void;
}): ColumnDef<BeneficiaryWithRelations>[] {
  return [
    {
      accessorKey: 'firstName',
      header: 'First Name',
      cell: ({ row }) => (
        <div className="whitespace-normal break-words max-w-xs">{row.getValue('firstName')}</div>
      ),
    },
    {
      accessorKey: 'lastName',
      header: 'Last Name',
      cell: ({ row }) => (
        <div className="whitespace-normal break-words max-w-xs">{row.getValue('lastName')}</div>
      ),
    },
    {
      accessorKey: 'gender',
      header: 'Gender',
      cell: ({ row }) => {
        const val = row.getValue('gender');
        return val ? (val as string)[0].toUpperCase() + (val as string).slice(1) : '';
      },
    },
    {
      accessorKey: 'dateOfBirth',
      header: 'Date of Birth',
      cell: ({ row }) =>
        row.getValue('dateOfBirth')
          ? new Date(row.getValue('dateOfBirth')).toLocaleDateString()
          : '',
    },
    {
      accessorKey: 'email',
      header: 'Email',
      cell: ({ row }) => row.getValue('email') || '-',
    },
    {
      accessorKey: 'phone',
      header: 'Phone',
      cell: ({ row }) => row.getValue('phone') || '-',
    },
    {
      accessorKey: 'institution',
      header: 'Institution',
      cell: ({ row }) => {
        const institution = row.original.institution;
        return institution ? institution.name : '-';
      },
    },
    {
      accessorKey: 'beneficiaryMessages',
      header: 'Messages',
      cell: ({ row }) => {
        const messages = row.original.beneficiaryMessages ?? [];
        if (!messages.length) return '0';
        return (
          <span>
            {messages.length} (
            {messages.map((msg, i) => (
              <Button
                key={msg.id}
                variant="link"
                className="p-0 h-auto text-blue-700"
                style={{ fontSize: 12 }}
                onClick={() => onMessageIdClick && onMessageIdClick(msg.id)}
              >
                {msg.title || msg.id}
                {i < messages.length - 1 ? ', ' : ''}
              </Button>
            ))}
            )
          </span>
        );
      },
    },
    {
      accessorKey: 'beneficiaryRequests',
      header: 'Requests',
      cell: ({ row }) => {
        const requests = row.original.beneficiaryRequests ?? [];
        if (!requests.length) return '0';
        return (
          <span>
            {requests.length} (
            {requests.map((req, i) => (
              <Button
                key={req.id}
                variant="link"
                className="p-0 h-auto text-blue-700"
                style={{ fontSize: 12 }}
                onClick={() => onRequestIdClick && onRequestIdClick(req.id)}
              >
                {req.title || req.id}
                {i < requests.length - 1 ? ', ' : ''}
              </Button>
            ))}
            )
          </span>
        );
      },
    },
    {
      accessorKey: 'createdAt',
      header: 'Created At',
      cell: ({ row }) =>
        row.getValue('createdAt') ? new Date(row.getValue('createdAt')).toLocaleDateString() : '',
    },
    {
      accessorKey: 'updatedAt',
      header: 'Updated At',
      cell: ({ row }) =>
        row.getValue('updatedAt') ? new Date(row.getValue('updatedAt')).toLocaleDateString() : '',
    },
    {
      id: 'createdBy',
      header: 'Created By',
      cell: ({ row }) => {
        const createdBy = row.original.createdBy;
        if (!createdBy) return '-';
        return `${createdBy.firstName} ${createdBy.lastName}`;
      },
    },
    {
      id: 'actions',
      header: 'Actions',
      cell: ({ row }) => (
        <div className="flex flex-wrap gap-2">
          <Button type="button" size="sm" variant="outline" onClick={() => onEdit(row.original)}>
            Edit
          </Button>
          {onAddMessage && (
            <Button
              type="button"
              size="sm"
              variant="secondary"
              onClick={() => onAddMessage(row.original.id)}
            >
              Add Message
            </Button>
          )}
          {onAddRequest && (
            <Button
              type="button"
              size="sm"
              variant="secondary"
              onClick={() => onAddRequest(row.original.id)}
            >
              Add Request
            </Button>
          )}
        </div>
      ),
    },
    {
      id: 'delete',
      header: 'Delete',
      cell: ({ row }) => (
        <Button
          type="button"
          size="sm"
          variant="destructive"
          onClick={() => onDelete(row.original.id)}
        >
          Delete
        </Button>
      ),
    },
  ];
}
